import { DynamicObject } from 'lance-gg';

export default class Heal extends  DynamicObject {

    static get netScheme() {
        return Object.assign({
            // add serializable properties here
        }, super.netScheme);
    }

    constructor(gameEngine, options, props) {
        super(gameEngine, options, props);
        this.class = Heal;
        this.type = "Heal";
        this.width = 1;
        this.height = 1;
        this.isStatic = 1;
    };

    syncTo(other) {
        super.syncTo(other);
    }

    collidesWith(other){
        return false;
    }

}